import { userAPI } from "../../API/api";

const FOLLOW = 'FOLLOW'; //    action
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE'
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT'
const TOGGLE_ISFETCHING = 'TOGGLE_ISFETCHING'
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS'





// let initialState = { //          

    
//     users: [
//       { id: 0, photoURL: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS5K-OfGEfPT08Nk-71FZa4wPOp4W3BD1cGkw&usqp=CAU',
//          followed: false, name: "Vitalii", status: 'i am a Boss', location: {city: 'Lviv', country: 'Ukraine'} },
//       { id: 1, photoURL: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS5K-OfGEfPT08Nk-71FZa4wPOp4W3BD1cGkw&usqp=CAU',
//         followed: true, name: "Denys", status: 'friend', location: {city: 'Kyiv', country: 'Ukraine'} },
//       { id: 2, photoURL: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS5K-OfGEfPT08Nk-71FZa4wPOp4W3BD1cGkw&usqp=CAU',
//         followed: false, name: "Sofia", status: 'sister', location: {city: 'Krakiv', country: 'Poland'} },
//     ],
//   }


    let initialState = {
      users: [], //            
      pageSize: 7, //      7
      totalUsersCount: 0,
      currentPage: 1, //         1   
      isFetching: true,
      followingInProgress: [],
      portionSize: 15

    };

const usersReducer = (state = initialState, action) => {

    switch (action.type) {
      case FOLLOW: //  actiontype follow -      
      return {
        ...state, //   users  1 user
        users: state.users.map(u => {
          //    
          if (u.id === action.userID) {
            //  id 
            return { ...u, followed: true }; //      followed  true
          }

          return u;
        })      
      
      };


      case UNFOLLOW:
        return {
          ...state, //   users  1 user
          users: state.users.map((u) => {
            //    
            if (u.id === action.userID) {
              //  id 
              return { ...u, followed: false }; //      followed  false
            }

            return u;
          }),
        };

        case SET_USERS: { //     ,        , 
          //      (  )       action
          // return {...state, users: [ ...state.users, ...action.users ]}
          return {...state, users: action.users}
        }

        case SET_CURRENT_PAGE: { //     ,       ,          action
          // return {...state, users: [ ...state.users, ...action.users ]}
          return {...state, currentPage: action.currentPage}
        }
        case SET_TOTAL_USERS_COUNT: { //     ,       ,          action
          // return {...state, users: [ ...state.users, ...action.users ]}
          return {...state, totalUsersCount: action.count}
        }
        case TOGGLE_ISFETCHING: {  //    -      
          return {...state, isFetching: action.isFetching}
        }
        case TOGGLE_IS_FOLLOWING_PROGRESS: {  //    -      
          return {...state, 
            followingInProgress: action.isFetching 
            ? [...state.followingInProgress, action.userID] 
            : state.followingInProgress.filter(id => id !== action.userID)
          }
        }
      


      default: return state;
    }


};

export const follow = (userID) => ({type: FOLLOW, userID}); //  -      action   
export const unfollow = (userID) => ({type: UNFOLLOW, userID}); // 
export const setUsers = (users) => ({type: SET_USERS, users}); //        
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage}); //        
export const setTotalUsersCount = (totaUserslCount) => ({type: SET_TOTAL_USERS_COUNT, count: totaUserslCount}); //        
export const toggleIsFetching = (value) => ({type: TOGGLE_ISFETCHING, isFetching: value}); //        
export const toggleFollowingProgress = (isFetching, userID) => ({type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userID}); //        




export const getUsersthunkCreator =  (page, pageSize) => {

  return (dispatch) => {

    dispatch(toggleIsFetching(true));


    userAPI.getUsers(page, pageSize).then((data) => {
        dispatch(toggleIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
      });

  }

}


export const followthunkCreator =  (userID) => {

  return (dispatch) => {

    dispatch(toggleFollowingProgress(true, userID)) 
    userAPI.follow(userID)
    .then((response) => {
      if (response.data.resultCode === 0) {
        dispatch(follow(userID));
      }
      dispatch(toggleFollowingProgress(false, userID))
    });

}

}


export const unfollowthunkCreator =  (userID) => {

  return (dispatch) => {

    dispatch(toggleFollowingProgress(true, userID)) 
    userAPI.unfollow(userID)
    .then((response) => {
      if (response.data.resultCode === 0) {
        dispatch(unfollow(userID));
      }
      dispatch(toggleFollowingProgress(false, userID))
    });

}

}

export default usersReducer;




