
import React, { useState } from "react";
import styles from './Paginator.module.css'



let Paginator = ({currentPage, onPageChange, pageSize, portionSize,  totalUsersCount}) => {
  //     return         
  //        ,           
  //       for

  let pageCount = Math.ceil(totalUsersCount / pageSize); //           
  //    totalUsersCount (    API   )      
  //  7 (        user-reducer)

  let pages = []; //              
  //     -  [1,2,3,...99,100]      700   10  
  //        for 

  for (let i = 1; i <= pageCount; i++) {
    // ''     ,          pages
    pages.push(i);
  }
  //  

  //  2  -       ,  -    

  
  let portionCount = Math.ceil(pageCount / portionSize) //      
  

  const [portionNumber, setPortionNumber] = useState(1 ) // portionNumber      

  const leftPortionPageNumber = (portionNumber - 1)* portionSize + 1; //  4     22  

  const rightPortionNumber = portionNumber * portionSize




  return (
    <div className={styles.paginator}>


      { portionNumber > 1 && 
      <button onClick={() => { setPortionNumber(portionNumber -1)  }}> PREV </button>}



      { pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionNumber)
      
      
      
      
      .map((p) => {
        //    (p)   -         p,    span
        //       onclick      (   ),   
        //   current    -  selected  classname
        return (
          <span
          key={p}
            onClick={() => {
              onPageChange(p);
            }}
            className={`${currentPage === p ? styles.selectedPage : undefined} ${styles.pageNumber}`}
          >
            {p}
          </span>
        );
      })}
       { portionCount > portionNumber  && 
      <button onClick={() => { setPortionNumber(portionNumber + 1)  }}> NEXT </button>}

    </div>
  );
}
export default Paginator;

