import {  combineReducers} from 'redux';
import profileReducer from './profile-reducer';
import dialogsReducer from './dialogs-reducer';
import sidebarReducer from './sidebar-reducer';
import usersReducer from './users-reducer';
import authReducer from './auth-reducer';
import thunkMiddleWare from 'redux-thunk'
import { configureStore } from '@reduxjs/toolkit'
import { reducer as formReducer } from 'redux-form' //      



let reducers = combineReducers({    //         reducers
    profilePage: profileReducer,  // combineReducers    store,     profilePage     profileReducer
    dialogsPage: dialogsReducer,
    sidebar: sidebarReducer,
    usersPage: usersReducer,//     usersPage
    auth: authReducer,
    form: formReducer //         
});

//      dialogsPage      dialogsReducer

//     profilePage        profileReducer     initialState

// let store = createStore(reducers, applyMiddleware(thunkMiddleWare)); //          
// // //  configureStore  state    3   profilePage, dialogsPage, sidebar

let store = configureStore( //       reducer    ,  
// middleware       middleware.    createStore
    {
        reducer: reducers,
        middleware: [thunkMiddleWare]
            
    }
) //       


window.store = store;

export default store;


