import { profileAPI } from "../../API/api";

const ADD_POST = 'ADD-POST'; //    action

const SET_USER_PROFILE = "SET_USER_PROFILE"
const SET_STATUS = "SET_STATUS"
const DELETE_POST = "DELETE_POST"


//          initialState.     
//      action           
//                ,    
//                 


let initialState = { //          . 
    postsData: [
      { id: 0, message: "Bla", count: 23 },
      { id: 1, message: "Bla bla  ", count: 13 },
      { id: 2, message: "Bla bla bla ", count: 14 },
      { id: 3, message: "Bla blaasd bla ", count: 14 },
    ],

    profile: null,
    status: ''
  }

// react     ,       ,    initialState


const profileReducer = (state = initialState, action) => { //  ( )     action    

    switch (action.type) {
      case ADD_POST: {
        let newPost = {
          //   
          id: 5,
          message: action.newPostText,
          count: 0,
        };

        let stateCopy = {...state};
        stateCopy.postsData = [...state.postsData]


        stateCopy.postsData.push(newPost); //     
        stateCopy.newPostText = ""; //  
        return stateCopy;
      };
      case SET_USER_PROFILE: {
        let stateCopy = {...state}
        stateCopy.profile = action.profile
        return stateCopy
       
      };
      case SET_STATUS: {
        let stateCopy = {...state}
        stateCopy.status = action.status
        return stateCopy
       
      };
      case DELETE_POST: {
        return {...state, postsData: state.postsData.filter(p => p.id !== action.postID)}
      };

        default: return state; //       
    }


};




export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile })
export const setStatus = (status) => ({type: SET_STATUS, status })
export const addPostClickActionCreator = (newPostText) => ({type: ADD_POST, newPostText}); //

export const deletePost = (postID) => ({type: DELETE_POST, postID})


export const getProfileThunk = (userId) => { //      id     getProfile    
  //      actioncreatora setUserProfile.           
  return (dispatch) => {
    profileAPI.getProfile(userId)
    .then((response) => {
      dispatch(setUserProfile(response.data));
    });
  }
}

export const getStatus = (userId) => { //      id     getProfile    
  //      actioncreatora setUserProfile.           
  return (dispatch) => {
    profileAPI.getStatus(userId)
    .then((response) => {
      dispatch(setStatus(response.data));
      
    });
  }
}
export const updateStatus = (status) => { //      id     getProfile    
  //      actioncreatora setUserProfile.           
  return (dispatch) => {
    profileAPI.updateStatus(status)
    .then((response) => {
      if(response.data.resultCode === 0) {
        dispatch(setStatus(status));

      }
    });
  }
}




export default profileReducer;